import 'antd/dist/antd.css';

import React from 'react';
import ReactDOM from 'react-dom';

import '../index.css';
import {
    Layout,
    Menu,
    Form,
    Input,
    message,
    Select
} from 'antd';

import {
    UploadOutlined,
    UserOutlined,
    VideoCameraOutlined
} from '@ant-design/icons';

import {
    Upload,
    Button
} from 'antd';

export default class PricePage extends React.Component {

    constructor(props) {
        super (props);

        this.state = {
            sheetname : "",
            requestings : [],
            downloadLink : "",
            downloadName : "",
            buttonText : "",
        }
        
        this.formRef = React.createRef();

        this.children = [];
        for (let i = 'A'.charCodeAt(0); i <= 'Z'.charCodeAt(0); i++) {
            this.children.push(<Select.Option key={String.fromCharCode (i)}>{String.fromCharCode (i)}</Select.Option>);
        }
    }

    renderSheetName () {

        if (this.state.sheetname == "" || !this.state.sheetname) {
            return ;
        }

        return (
            <Form.Item label="" name="sheet_name" rules={[{ required: true, message: '' }]}>
                <Input 
                    placeholder="" 
                    disabled={true} 
                />
            </Form.Item>
        )
    }

    renderDownloadLink () {

        if (this.state.downloadName == "" || !this.state.downloadName) {
            return ;
        }

        let downloadLink = "http://127.0.0.1:8000/download?file=" + this.state.downloadName;

        return (
            <Form.Item>
                <Button type="link" href={downloadLink}  onClick={() => {
                    this.setState ({downloadName : ""})
                }}> {this.state.downloadName}</Button>
            </Form.Item>
        )
    }

    onSubmitClick () {

        let maps = this.formRef.current.getFieldsValue ();
        let formdata = new FormData ();

        let requestings  = this.state.requestings || [];

        requestings [0] = true;
        this.setState ({
            requestings : requestings,
            buttonText : "...",
        })

        formdata.append ("name_column",maps ["name_column"]);
        formdata.append ("now_column",maps ["now_column"]);
        formdata.append ("org_column",maps ["org_column"]);
        formdata.append ("sheet_name",maps ["sheet_name"]);
        formdata.append ("src_file",maps ["src_file"].file.originFileObj);

        fetch ("http://localhost:8000/export_price/",{
            method : "POST",
            headers: {
                // 'Accept': 'application/json, application/xml, text/plain, text/html, *.*',
                // 'Content-Type': 'multipart/form-data'
            },
            body : formdata,
        }).then (resp => {
            return resp.json ()
        }).then (res => {
            console.log (res);

            if (res.ret == 0) {
                message.info ("")
            } else {
                message.error (" : " + res.msg);
            }

            requestings [0] = false;
            this.setState ({
                requestings : requestings,
                buttonText : "",
                downloadName : res.data,
            })

        }).catch (error => {
            // console.log (error);
            message.error (error);
        })
    }


    render() {
        return (
            <div className="site-layout-background" style={{ padding: 24, minHeight: 360 }}>
                <Form
                    labelCol={{ span: 4 }}
                    wrapperCol={{ span: 4 }}
                    ref={this.formRef}
                    // layout={formLayout}
                    layout='horizontal'
                    onFinish={this.onSubmitClick.bind(this)}
                >
                    <Form.Item
                        label=""
                        name="src_file"
                        rules={[{ required: true, message: '' }]}
                    >
                        <Upload
                            multiple={false}
                            maxCount={1}
                            name='src_file'
                            disabled={this.state.requestings[0]}
                            rules={[{ required: true, message: '' }]}
                            accept=".xlsx"
                            action={"http://localhost:8000/sheetnames/"}
                            onChange={(evt) => {
                                let fileList = evt.fileList;
                                let file = evt.file;

                                if (file.status == "success") {
                                    // message.success ("")
                                } else if (file.status == "error") {
                                    message.error("");
                                } else if (file.status == "done") {
                                    // console.log (file.response);
                                    if (file.response.ret == 0) {
                                        // console.log (file.response);

                                        this.setState({
                                            sheetname: file.response.data[0],
                                        })

                                        this.formRef.current.setFieldsValue({
                                            sheet_name: file.response.data[0],
                                        });

                                    } else {
                                        message.error(file.response.msg);
                                    }
                                }
                            }}
                        >
                            <Button icon={<UploadOutlined />} disabled={this.state.requestings[0]} ></Button>
                        </Upload>
                    </Form.Item>

                    {this.renderSheetName()}

                    <Form.Item
                        label=""
                        rules={[{ required: true, message: '' }]}
                        name="name_column"
                    >
                        <Select style={{ width: '100%' }} disabled={this.state.requestings[0]} placeholder="" onChange={(evt) => {

                        }}>
                            {this.children}
                        </Select>
                    </Form.Item>
                    <Form.Item
                        label=""
                        name="org_column"
                        rules={[{ required: true, message: '' }]}
                    >
                        <Select style={{ width: '100%' }} disabled={this.state.requestings[0]} placeholder="" onChange={(evt) => {

                        }}>
                            {this.children}
                        </Select>
                    </Form.Item>
                    <Form.Item
                        label=""
                        name="now_column"
                        rules={[{ required: true, message: '' }]}
                    >
                        <Select style={{ width: '100%' }} disabled={this.state.requestings[0]} placeholder="" onChange={(evt) => {

                        }}>
                            {this.children}
                        </Select>
                    </Form.Item>

                    {this.renderDownloadLink()}

                    <Form.Item>
                        <Button type="primary" htmlType="submit" loading={this.state.requestings[0]}>{this.state.buttonText}</Button>
                    </Form.Item>
                </Form>
            </div>
        )
    }
}